<template>
<div id="box">
    <div class="nav">
		  <span @click="toggle_order(0)" :class="{active:index==0}">所有订单</span>
		  <span @click="toggle_order(1)" :class="{active:index==1}">待付款</span>
		  <span @click="toggle_order(2)" :class="{active:index==2}">待发货</span>
		  <span @click="toggle_order(3)" :class="{active:index==3}">已发货</span>
		  <span @click="toggle_order(4)" :class="{active:index==4}">已完成</span>
	  </div>

    <div class="orderList">
      <van-list finished-text="没有更多了" v-model="loading" :finished="finished" @load="getData()">
        <div class="list" v-for="(item,index1) in data" :key="index1">
            <div class="head flex-between" style="margin:0 20pt;">
              <p class="number">订单号：<span>{{item.order_sn}}</span></p>
              <p class="status" v-if="item.order_status==0" style="color: #F8900A;">{{item.order_status_detail}}</p>
              <p class="status" v-if="item.order_status==1" style="color: #F8900A;">{{item.order_status_detail}}</p>
              <p class="status" v-if="item.order_status==2" style="color: #F8900A;">{{item.order_status_detail}}</p>
              <p class="status" v-if="item.order_status==3" style="color: #F8900A;">{{item.order_status_detail}}</p>
              <p class="status" v-if="item.order_status==4" style="color: #F8900A;">{{item.order_status_detail}}</p>
            </div>
            <div class="item">
              <!--  多个时在这里循环列表   -->
			        <div id="flex_info" @click="urlto(item.order_status,item.order_id)" v-for="(item,index) in item.order_goods" :key="index">
              	<img class="img" :src="item.original_img" alt="">
              	<div class="flex_1">
              	  <p class="title">{{item.goods_name}}</p>
              	  <p class="text" style="display:block;margin:10pt 0;"></p>
              	  <p class="price">￥{{item.goods_price}} &nbsp;&nbsp;&nbsp;&nbsp;<span> x {{item.goods_num}}</span></p>
              	</div>
			        </div>
              <div class="total flex-center-y" v-if='order_type == 1' >
                总计：<span>￥{{Number(item.total_amount)}}</span>
              </div>
              <div class="button flex-center-y">
                <div class="border flex-center" @click="kefu()">联系客服</div>
                <!-- <div class="border flex-center" v-if="item.order_status==2" @click="$router.push({path:'/chakanwuliu',query:{id_:item.order_id}})">查看物流</div> -->
                <!-- <div class="background_blue flex-center" v-if="item.order_status==1&&item.shipping_status==0" @click="tixing(item.order_id)">提醒发货</div> -->
                <div class="border flex-center" v-if="item.pay_status==0" @click="cancel(item.order_id,index1)">取消订单</div>
                <div class="background flex-center" v-if="item.order_status==0" @click="up_order(item.order_id)">去付款</div>
                <div class="background_blue flex-center" v-if="item.order_status==1&&item.shipping_status==1" @click="submit(item.order_id)">确认收货</div>
              </div>
            </div>
        </div>
      </van-list>
  </div>
</div>
</template>

<script>
import { order_list, del_order, kefu, mysure } from '@/axios/getData'
import { Tab, Tabs, List, Dialog, Toast, NavBar } from 'vant';
export default {
  components: {
    'van-list': List,
  },
  data(){
	  return{
      index:0,
      data:[
        {}
      ],
      list:[],
      loading: false,
      finished: false,
      type:['','WAITPAY',"WAITSEND",'WAITRECEIVE',"FINISH"],
      p:1,
      order_type: 1     //  商品分类  1 普通商品    2 乐拼商品
	  }
  },
  created(){
    // localStorage.setItem('openid','oHJ6Ow6_xrlYJfcUu29JQIwhkkJs')
    this.index = this.$route.query.index
    this.getData();
  },
  methods:{
    sure_order(id){
      mysure({order_id: id}).then((res)=>{
        this.toggle_order(5);
      })
    },
    // 跳转订单详情
    urlto(a,id){
      // if( a == 3 ) return;          ',query:{id:item.order_id
      this.$router.push({path:'/order_detail',query:{id}}) 
    },
    // 提醒发货
    tixing(id){
      ask_send_item({order_id:id}).then((res)=>{
        Toast('提醒成功，更多帮助请联系客服')
      })
    },
    getData(){
      if( this.loading ){
        var data = {};
        data.type = this.type[this.index];
        data.p = this.p
        order_list(data).then(res => {
          if( this.p ==1 ){
            this.data = res.data;
          } else {
            this.data = this.data.concat(res.data)
          }
          console.log(this.data,res.data)
          this.p++;
          this.loading = false;
          if( this.data.length==res.count ){
            console.log( this.data.length )
            this.finished = true
          }
          // this.finished = true   //测试用
        });
      }
    },
    // 删除订单
    cancel(id,index) {
      Dialog.confirm({
        title: '提示',
        message: '取消订单后不可恢复！'
      }).then(() => {
        let data = {};   
        data.id = id;
        del_order(data).then((res)=>{
          if(res.code==200){
            Toast('取消成功')
            this.data.splice(index,1);
          } else {
            Toast('取消失败')
          }
        })
      }).catch(() => {
        // on cancel   不加会报异常
      });
    },
    //  切换订单状态 
    toggle_order(a){            //  切换导航栏
      this.index=a;             //  订单状态  导航栏  下标
      this.p=1;                 //  重置页码
      this.finished=false;      //  开启上拉加载
      this.loading=true;        //  设为false不会再次加载
      this.getData();           //  请求数据
    },
    //  联系客服
    kefu(){
      var that = this;
      if(that.$store.state.kefu){
        Dialog.confirm({
          title: that.$store.state.kefu.title,
          message: that.$store.state.kefu.number
        }).then(() => {
          // 不加回报异常          
        }).catch(() =>{
          // 不加回报异常
        })
        return;
      }
      kefu().then(res =>{
        that.$store.state.kefu = res.data
        Dialog.confirm({
          title: res.data.title,
          message: res.data.number
        }).then(() => {
          // 不加回报异常          
        }).catch(() =>{
          // 不加回报异常
        })
      })
    },
    //  确认收货
    submit(id) {
      var that = this;
      Dialog.confirm({
        title: '提示',
        message: '请检查订单无误后确认！'
      }).then(() => {
        //  点击确认收货
        mysure({id}).then((res)=>{
          console.log(res)
        })
      }).catch(()=>{
        //  不写会报异常
      })
    },
    //  去付款
    // up_order(aa){
    //   let params = new URLSearchParams();
    //   params.append("openid", localStorage.getItem("openid"));
    //   params.append("order_id", aa);
    //  支付
    //   axios.post(url.liji, params).then(res => {
    //     console.log(res);
    //     if (typeof WeixinJSBridge == "undefined") {
    //       if (document.addEventListener) {
    //         document.addEventListener(
    //           "WeixinJSBridgeReady",
    //           this.onBridgeReady(res),
    //           false
    //         );
    //       } else if (document.attachEvent) {
    //         document.attachEvent(
    //           "WeixinJSBridgeReady",
    //           this.onBridgeReady(res)
    //         );
    //         document.attachEvent(
    //           "onWeixinJSBridgeReady",
    //           this.onBridgeReady(res)
    //         );
    //       }
    //     } else {
    //       this.onBridgeReady(res);
    //     }
    //   });
    // },
    onBridgeReady(res) {
      WeixinJSBridge.invoke(
        "getBrandWCPayRequest",
        {
          appId: res.data.data.appId, //公众号名称，由商户传入
          timeStamp: res.data.data.timeStamp, //时间戳，自1970年以来的秒数
          nonceStr: res.data.data.nonceStr, //随机串
          package: res.data.data.package,
          signType: res.data.data.signType, //微信签名方式：
          paySign: res.data.data.paySign //微信签名
        },
        res => {
          if (res.err_msg == "get_brand_wcpay_request:ok") {
            this.$router.push({ path: "/myOrder",query:{index:2} });
          }
        }
      );
    }
  }
}
</script>

<style scoped lang="scss">
#box{
  width:100%;
}
.orderList {
  margin-top: 25px;
  padding:20pt;
}
.list{
  background: #fff;
  margin-bottom:15pt;
  box-shadow:0px 0px 10px rgba(167, 165, 165, 0.5);
  border-radius: 6pt;
  padding-top:5pt;
}
.list a{
	color:#000;
}
.list .img{
  width:60pt;
  height:60pt;
  margin-right:50px;
}
.list .item {
  margin: 0 30px;
  border-radius: 20px;
  display: block;
}
.list .info {
  padding: 20px;
  background: #fafafa;
}
.list img {
  width: 120px;
  height: 120px;
  margin-right: 25px;
  border-radius: 6px;
}
.list .flex_1 {
  overflow: hidden;
}
.list .text {
  font-size: 22px;
  color: #848484;
  margin: 6px 0;
}
.price {
  color: #848484;
}
.total {
  text-align: right;
  padding:20px;
  justify-content: flex-end;
}
.list span {
  color: #ef1818;
  font-weight: bold;
}
.button {
  display: flex;
  justify-content: flex-end;
  padding-bottom: 30px;
  // border-bottom: 1pt solid rgba(153, 153, 153, .3);
}
.button div {
  text-align: center;
  width: 23%;
  padding:5pt 0;
  border-radius: 30px;
  margin-right: 20px;
  font-size: 15px;
}
.button div:last-child {
  margin-right: 0;
}
.button .border {
  border: 2px solid #efefef;
  box-sizing: border-box;
  color: #545454;
}
.button .background {
  background: #FF4C23;
  color: #fff;
}
.button .background_blue  {
  background: linear-gradient(to right, #21cce6 , #2697e7);
  color: #fff;
}

.head{
  display: flex;
  justify-content: space-between;
}







.nav{
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	margin: 13px;
	a{
		color:#000; 
	}
	.active{
		position: relative;
		color:#FF4C23;
	}
	.active::after{
		position: absolute;
		content: "";
		width: 100%;
		height: 0.1rem;
		background: #FF4C23;
		left: 0;
		bottom: -10px;
	}
}

#flex_info{
	display: flex;
	flex-direction: row;
	align-items: center;
}

.orderList /deep/ .van-loading{   /*从组件父级选择器开始deep才有效果*/
  background: transparent !important;
}

[v-cloak]{
  display: none;
}

.bottom{
  padding: 10pt 20pt;
  display: flex;
  justify-content: space-between;
  ul{
    li:nth-child(1){
      display: inline-block;
      margin-bottom:3pt;
      color:#636363;  
    }
    li:nth-child(2){
      color:#EF1818;
      font-weight: 600;
      text-align: center;
    }
  }
}
</style>



